html {
    font-size: 100%;
    // Defines what 1rem should be
    // font-size: 10px -- easy way to convert to rem. In % 62.5%
}

html,
body {
    -webkit-text-size-adjust: 100%;
    font-variant-ligatures: none;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-smooth: antialiased;
    text-shadow: rgba($black, 0.01) 0 0 1px;
    font-size: 16px;
    font-family: $default-font;
    font-weight: 400;
    color: $black;
}

h1,
h4,
h5,
h6 {
    font-weight: 700;
    margin: 0;
}

h1 {
	font-size: 38px;
    line-height: 1.175;
    font-weight: 500;
}

h2 {
	font-size: 34px;
    font-weight: 500;
	line-height: 1.2;

    @include media-up(m) {
        line-height: 1.3;
    }
}

h3 {
    font-size: 20px;
    line-height: 1.2;
    font-weight: 400;

    @include media-up(m) {
        font-size: 25px;
    }
}

h4 {
	font-size: 22px;
	line-height: 1.4;
    font-weight: 500;
}

h5,
h6 {
    font-size: 20px;
    line-height: 1.4;
    font-weight: 500;
}

h6 {
    font-weight: 400;
}

p {
    margin: 0;
    line-height: 1.2;

    @include media-up(m) {
        line-height: 1.34;
    }
}

hr {
    border: 1px solid darken($white, 30);
}