#page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}
#page #content {
  position: relative;
  flex: 1;
}

html {
  font-size: 100%;
}

html,
body {
  -webkit-text-size-adjust: 100%;
  font-variant-ligatures: none;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smooth: antialiased;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: #000;
}

h1,
h4,
h5,
h6 {
  font-weight: 700;
  margin: 0;
}

h1 {
  font-size: 38px;
  line-height: 1.175;
  font-weight: 500;
}

h2 {
  font-size: 34px;
  font-weight: 500;
  line-height: 1.2;
}
@media (min-width: 768px) {
  h2 {
    line-height: 1.3;
  }
}

h3 {
  font-size: 20px;
  line-height: 1.2;
  font-weight: 400;
}
@media (min-width: 768px) {
  h3 {
    font-size: 25px;
  }
}

h4 {
  font-size: 22px;
  line-height: 1.4;
  font-weight: 500;
}

h5,
h6 {
  font-size: 20px;
  line-height: 1.4;
  font-weight: 500;
}

h6 {
  font-weight: 400;
}

p {
  margin: 0;
  line-height: 1.2;
}
@media (min-width: 768px) {
  p {
    line-height: 1.34;
  }
}

hr {
  border: 1px solid #b3b3b3;
}

.text-center {
  text-align: center;
}

.text-uppercase {
  text-transform: uppercase;
}

.clearfix :after {
  display: block;
  content: "";
  clear: both;
}

.autoprefixer-test {
  user-select: none;
}